* {
  user-select: none;
}
.body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  box-sizing: border-box;
  /* user-select: none; */
}
section {
  padding: 20px;
}
.grey {
  color: rgb(138, 123, 123);
}
.bold {
  font-weight: bold;
}
.header {
  width: 100vw;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
}
.logo {
  padding: 22px 30px;
}
.main {
  padding: 30px;
  display: flex;
  flex-direction: column;
  /* justify-content: flex-end; */
}
.main-image {
  max-width: 400px;
  align-self: center;
  width: 70vw;
  height: 40vh;
}
main-header {
  margin-bottom: 16px;
}
main-text {
  margin-top: 0;
}
.cards-list {
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
}
.card {
  position: relative;
  max-width: 175px;
  font-size: 12px;
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
}
.card-main-image {
  width: 100%;
  border-radius: 10px;
  margin-bottom: 9px;
}
.card-title {
  overflow: hidden;
  text-overflow: ellipsis;
}
.card-rating {
  display: flex;
  align-items: center;
}
.card-star-icon {
  height: 14px;
}
.card-price {
  margin-top: auto;
}
.status {
  position: absolute;
  background-color: aliceblue;
  margin: 5px 5px;
  border-radius: 3px;
  padding: 4px;
  font-weight: bold;
  top: 5px;
  left: 5px;
}
